@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');


.navbar {
    width: 100%;
    height: 70px;
    position: absolute;
    z-index: 10;
    background: rgb(255, 255, 255);

    box-shadow: 1px 3px 9px -2px rgba(0,0,0,0.75);
    -webkit-box-shadow: 1px 3px 9px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 3px 9px -2px rgba(0,0,0,0.75);
}

.navbar .navbar-container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.navbar-logo img {
  padding: 10px;
}

.navbar .icon {
  font-size: 1.4rem;
  color: #119bd1;
}

.navbar .logo .icon {
    font-size: 3rem;
    margin-right: 1rem;
    color: var(--primary-color);
}

.hamburger {
    display: none;
    padding: 1rem;
}

.navbar .nav-menu {
    height: 100%;
    align-items: center;
    display: flex;
    /* padding: 2rem 0; */
}

.navbar li {
    font-size: 17px;
    width: max-content;
    padding: .5rem 1rem;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 600;
}

  .hide {
    padding: 5px 20px;
    background-color: #119bd19d;
  }

  .hide p {
    font-size: 13px;
    font-weight: 400;
    color: rgb(37, 37, 37);
  }

  .hide p :hover {
    color: rgb(160, 80, 6);
  }

  .hide span {
    color: rgb(250, 250, 250);
    font-size: 18px;
    margin: auto;
  }

  .menu-item .sub__menus li a {
    color: #353535;
    font-size: 13px;
    padding: -1rem ;
  }

  .menu-item .sub__menus li a:hover {
    color: rgb(255, 123, 0) !important;
  }

/* -----------------------------------------------------> SUB MENU */
.sub__menus {
    position: absolute;
    display: none;
    background: white;
    border: 1px solid lavender;
    border-radius: 10px;
    width: 250px;
    left: -17px;
    padding: 2px 0 0 0;
    margin-top: 6px;
    z-index: 1000;
  }

  /* -----------------------------------------------------> DROPDOWN ARROW */
  .sub__menus__arrows {
    position: relative;
  }

  .sub__menus__arrows svg, .sub__menus__arrows__full svg {
    padding-top: 0px;
    position: absolute;
    top: 10px;
  }


  
@media screen and (max-width: 940px) {
    .navbar .hamburger {
        display: block;
        z-index: 15;
    }

    .navbar .nav-menu {
        position: absolute;
        flex-direction: column;
        width: 100%;
        height: 80vh;
        top: 0;
        left:-100%;
        justify-content: center;
        background: var(--primary-color);
        transition: 0.5s ease-in;
    }

    .navbar .active {
        left: 0;
    }

    .navbar li {
        font-size: 1rem;
    }

    .navbar button {
        font-size: 1.8rem;
    }

    .menu-item .sub__menus li a {
        font-size: 14px;
      }

      li .submenu-header {
        font-size: 14px;
      }

      #submenu-item li {
        padding: .2rem 1rem;
      }

      .sub__menus {
        width: 250px;
      }

      .navbar .navbar-container {
        justify-content: space-between;
      }

      .hide {
        display: none;
      }

      .navbar-container .navbar-logo img {
        width: 150px;
      }

}





